import React from "react";
import { Link } from "react-router-dom";
import server from "../ServerInterface/server";
import Entry from "./Entry";
import "./Home.css";

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = { username: "", entries: [], cursor: 0 };
  }

  render() {
    let username = "";
    let score = "";
    const location = this.props.location;
    if (location) {
      if (location.state) {
        if (location.state.user) {
          username = location.state.user;
        }
      }
    }
    //alert(location.final);
    if (location) {
      if (location.state) {
        if (location.state.score) {
          score = location.state.score;
        }
      }
    }
    return (
      <div>
        <div className="loginButton">
          {username.length > 0 ? (
            username
          ) : (
            <Link to="/login" className="button">
              Login
            </Link>
          )}
        </div>
        <div className="title">Image Quiz</div>
        <div className="info"> Click on an Image to Start a Quiz</div>
        <div>{score.length > 0 ? score : ""}</div>
        <div className="body">
          <Link to="/quiz1">
            <img src={require("./images/rose.png")} alt="daisy" />
          </Link>
          <Link to="/quiz2">
            <img src={require("./images/sunflower.png")} alt="sunflower" />
          </Link>
          <Link to="/quiz3">
            <img src={require("./images/tulip.png")} alt="tulip" />
          </Link>
        </div>
      </div>
    );
  }
}

export default Home;
